.Header {
    /* width: 100%; */
    width: 1920px;
    height: 125px;
    background: var(--unnamed-color-3c3c3b) 0% 0% no-repeat padding-box;
    opacity: 0.8;
    position: sticky;
    top: 0;
    z-index: 1;
    transition: opacity .25s ease-in-out 0s,visibility .25s ease-in-out 0s;
}

.BurgerMenu {
    display: none;
    position: fixed;
    width: 100%;
    top: 100px;
    height: 79.5%;
    background: #E94E1B 0% 0% no-repeat padding-box;
    box-shadow: 5px 10px 15px #00000029;
    border-radius: 0px 0px 50px 0px;
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-300) var(--unnamed-font-size-35)/var(--unnamed-line-spacing-53) Sofia Pro Light;
    letter-spacing: var(--unnamed-character-spacing-0);
    color: var(--unnamed-color-ffffff);
    z-index: 1;
}

